import React from 'react';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import { toast } from 'react-toastify';
import { Container, Row, Col, Button, Card } from 'react-bootstrap';

const SERVER = process.env.REACT_APP_SERVER || 'http://localhost:8080';

const Register = () => {
  return (
    <Container>
      <Card className='mt-5'>
        <Row className="p-5 justify-content-md-center">
          <Col md="6">
            <h2>Înregistrare</h2>
            <Formik
              initialValues={{ name: '', email: '', password: '', phone: '' }}
              validationSchema={Yup.object({
                name: Yup.string().required('Numele este obligatoriu'),
                email: Yup.string().email('Email invalid').required('Emailul este obligatoriu'),
                password: Yup.string().min(6, 'Parola trebuie să aibă cel puțin 6 caractere').required('Parola este obligatorie'),
                phone: Yup.string().required('Telefonul este obligatoriu')
              })}
              onSubmit={(values, { setSubmitting }) => {
                axios.post(SERVER + '/api/auth/register', values)
                  .then(response => {
                    toast.success('Înregistrare reușită!');
                    setSubmitting(false);
                  })
                  .catch(error => {
                    toast.error('Eroare la înregistrare: ' + error.message);
                    setSubmitting(false);
                  });
              }}
            >
              {({ isSubmitting }) => (
                <Form>
                  <div className="form-group">
                    <label htmlFor="name">Nume</label>
                    <Field name="name" type="text" className="form-control" />
                    <ErrorMessage name="name" component="div" className="text-danger" />
                  </div>
                  <div className="form-group">
                    <label htmlFor="email">Email</label>
                    <Field name="email" type="email" className="form-control" />
                    <ErrorMessage name="email" component="div" className="text-danger" />
                  </div>
                  <div className="form-group">
                    <label htmlFor="password">Parolă</label>
                    <Field name="password" type="password" className="form-control" />
                    <ErrorMessage name="password" component="div" className="text-danger" />
                  </div>
                  <div className="form-group">
                    <label htmlFor="phone">Telefon</label>
                    <Field name="phone" type="text" className="form-control" />
                    <ErrorMessage name="phone" component="div" className="text-danger" />
                  </div>
                  <Button type="submit" disabled={isSubmitting} className="my-3 btn btn-primary">
                    Înregistrează-te
                  </Button>
                  <div className="links">
                    <p>Ai deja un cont? <a href="/login">Intră în cont</a></p>
                  </div>
                </Form>
              )}
            </Formik>
          </Col>
        </Row>
      </Card>
    </Container>
  );
};

export default Register;