import React from 'react';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import { toast } from 'react-toastify';
import { Container, Row, Col, Button, Card } from 'react-bootstrap';

const SERVER = process.env.REACT_APP_SERVER || 'http://localhost:8080';

const Login = () => {
  return (
    <Container>
      <Card className='mt-5'>
        <Row className="p-5 justify-content-md-center">
          <Col md="6">
            <h2>Autentificare</h2>
            <Formik
              initialValues={{ email: '', password: '' }}
              validationSchema={Yup.object({
                email: Yup.string().email('Email invalid').required('Emailul este obligatoriu'),
                password: Yup.string().required('Parola este obligatorie')
              })}
              onSubmit={(values, { setSubmitting }) => {
                axios.post(SERVER + '/api/auth/login', values)
                  .then(response => {
                    toast.success('Autentificare reușită!');
                    setSubmitting(false);
                    // Salvează token-ul JWT și redirecționează utilizatorul după autentificare
                    localStorage.setItem('token', response.data.token);
                    window.location.href = '/';
                  })
                  .catch(error => {
                    toast.error('Eroare la autentificare: ' + error.message);
                    setSubmitting(false);
                  });
              }}
            >
              {({ isSubmitting }) => (
                <Form>
                  <div className="form-group">
                    <label htmlFor="email">Email</label>
                    <Field name="email" type="email" className="form-control" />
                    <ErrorMessage name="email" component="div" className="text-danger" />
                  </div>
                  <div className="form-group">
                    <label htmlFor="password">Parolă</label>
                    <Field name="password" type="password" className="form-control" />
                    <ErrorMessage name="password" component="div" className="text-danger" />
                  </div>
                  <Button type="submit" disabled={isSubmitting} className="my-3 btn btn-primary">
                    Autentificare
                  </Button>
                  <div className="links">
                    <p>Nu ai cont? <a href="/register">Creează-ți unul!</a></p>
                  </div>
                </Form>
              )}
            </Formik>
          </Col>
        </Row>
      </Card>
    </Container>
  );
};

export default Login;