import React, { useState } from 'react';
import Draggable from 'react-draggable';
import FontPicker from './FontPicker';

const EditableText = ({ text, setText, style, setStyle }) => {
    const handleFontChange = (font) => {
        setStyle({ ...style, fontFamily: font });
    };

    return (
        <div>
        <FontPicker font={style.fontFamily} setFont={handleFontChange} />
        <Draggable>
            <div style={style} contentEditable suppressContentEditableWarning onBlur={(e) => setText(e.target.innerText)}>
                {text}
            </div>
        </Draggable>
    </div>
    );
};

export default EditableText;