import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card, Button, Form } from 'react-bootstrap';
import { toast } from 'react-toastify';
import api from '../api';
import { useParams } from 'react-router-dom';
import { saveAs } from 'file-saver';

const InvitationsPage = () => {
    const { eventId } = useParams();
    const [invitations, setInvitations] = useState([]);
    const [event, setEvent] = useState({});
    const [statusFilter, setStatusFilter] = useState(-1); // -1 pentru toate invitațiile

    useEffect(() => {
        fetchEvent();
        fetchInvitations();
    }, [eventId]);

    const fetchEvent = async () => {
        try {
            const response = await api.get(`/events/${eventId}`);
            setEvent(response.data);
        } catch (error) {
            toast.error('Eroare la încărcarea evenimentului: ' + error.message);
        }
    };

    const fetchInvitations = async () => {
        try {
            const response = await api.get(`/invitations/${eventId}`);
            setInvitations(response.data);
        } catch (error) {
            toast.error('Eroare la încărcarea invitațiilor: ' + error.message);
        }
    };

    const handleDeleteInvitation = async (invitationId) => {
        try {
            await api.delete(`/invitations/${invitationId}`);
            toast.success('Invitație ștearsă cu succes!');
            fetchInvitations();
        } catch (error) {
            toast.error('Eroare la ștergerea invitației: ' + error.message);
        }
    };

    const copyToClipboard = () => {
        const link = `${window.location.origin}/confirm/${eventId}`;
        navigator.clipboard.writeText(link)
            .then(() => {
                toast.success('Link copiat în clipboard!');
            })
            .catch((error) => {
                toast.error('Eroare la copierea linkului: ' + error.message);
            });
    };

    const handleStatusFilterChange = (e) => {
        setStatusFilter(Number(e.target.value));
    };

    const filteredInvitations = invitations.filter(invitation => {
        if (statusFilter === 2) return true; // Afișează toate invitațiile
        return invitation.status === statusFilter;
    });

    const generateConfirmedList = () => {
        const confirmedInvitations = invitations
            .filter(invitation => invitation.status === 1)
            .sort((a, b) => a.name.localeCompare(b.name));
        
        const content = confirmedInvitations.map((invitation, i) => `${i+1}. ${invitation.name}`).join('\n');
        const blob = new Blob([content], { type: 'text/plain;charset=utf-8' });
        saveAs(blob, `Listă_invitați_${event.name}.txt`);
    };

    return (
        <Container>
            <Row className="justify-content-md-center my-3">
                <Col md="8">
                    <h1>{event.name}</h1>
                    <Button variant="secondary" onClick={copyToClipboard}>
                        Copiază linkul de confirmare
                    </Button>
                    <Form.Group controlId="statusFilter" className="mt-3">
                        <Form.Label>Filtrează după status</Form.Label>
                        <Form.Control as="select" value={statusFilter} onChange={handleStatusFilterChange}>
                            <option value="2">Toate</option>
                            <option value="1">Confirmat</option>
                            <option value="0">Refuzat</option>
                            <option value="-1">În așteptare</option>
                        </Form.Control>
                    </Form.Group>
                    <Button variant="success" className="mt-3" onClick={generateConfirmedList}>
                        Descarcă lista confirmaților
                    </Button>
                </Col>
            </Row>
            <Row>
                {filteredInvitations.map((invitation) => (
                    <Col md="4" key={invitation._id}>
                        <Card className="mb-4">
                            <Card.Body>
                                <Card.Title>{invitation.name}</Card.Title>
                                <Card.Text>
                                    <strong>Status:</strong> {invitation.status === 1 ? "Confirmat" : (invitation.status === 0 ? "Refuzat" : "În așteptare")}
                                </Card.Text>
                                <Button variant="danger" onClick={() => handleDeleteInvitation(invitation._id)}>
                                    Șterge
                                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                ))}
            </Row>
        </Container>
    );
};

export default InvitationsPage;