import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card, Button, Modal, Form } from 'react-bootstrap';
import { Formik, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { toast } from 'react-toastify';
import api from '../api';
import { useAuth } from '../context/AuthContext';
import { useNavigate } from 'react-router-dom';
import { jwtDecode } from 'jwt-decode';

const EventsPage = () => {
    const navigate = useNavigate();
    const [imageBase64, setImageBase64] = useState(null);
    const { state } = useAuth();
    const [events, setEvents] = useState([]);
    const [showModal, setShowModal] = useState(false);
    const [showDeleteModal, setShowDeleteModal] = useState(false);
    const [showEditModal, setShowEditModal] = useState(false);
    const [selectedEvent, setSelectedEvent] = useState(null);

    useEffect(() => {
        const token = localStorage.getItem("token");

        if (token) {
            try {
                const decodedToken = jwtDecode(token);
                const currentTime = Date.now() / 1000;

                if (decodedToken.exp < currentTime) {
                    localStorage.removeItem("token");
                }
            } catch (error) {
                localStorage.removeItem("token");
            }
        }
    }, []);

    useEffect(() => {
        fetchEvents();
    }, []);

    const fetchEvents = async () => {
        try {
            const response = await api.get('/events');
            setEvents(response.data);
        } catch (error) {
            toast.error('Eroare la încărcarea evenimentelor: ' + error.message);
        }
    };

    const handleAddEvent = async (values, { setSubmitting, resetForm }) => {
        try {
            await api.post('/events', { ...values, userId: state.id, image: imageBase64 });
            toast.success('Eveniment adăugat cu succes!');
            setSubmitting(false);
            setShowModal(false);
            resetForm();
            fetchEvents();
        } catch (error) {
            toast.error('Eroare la adăugarea evenimentului: ' + error.message);
            setSubmitting(false);
        }
    };

    const handleDeleteEvent = async () => {
        try {
            await api.delete(`/events/${selectedEvent._id}`);
            toast.success('Eveniment șters cu succes!');
            setShowDeleteModal(false);
            fetchEvents();
        } catch (error) {
            toast.error('Eroare la ștergerea evenimentului: ' + error.message);
        }
    };

    const handleEditEvent = async (values, { setSubmitting }) => {
        try {
            await api.put(`/events/${selectedEvent._id}`, { ...values, image: imageBase64 });
            toast.success('Eveniment actualizat cu succes!');
            setSubmitting(false);
            setShowEditModal(false);
            fetchEvents();
        } catch (error) {
            toast.error('Eroare la actualizarea evenimentului: ' + error.message);
            setSubmitting(false);
        }
    };

    const handleImageChange = (e) => {
        const selectedImage = e.target.files[0];
        if (selectedImage) {
            const reader = new FileReader();
            reader.onloadend = () => {
                setImageBase64(reader.result);
            };
            reader.readAsDataURL(selectedImage);
        }
    };

    return (
        <Container>
            <Row className="justify-content-md-center my-3">
                <Col md="8">
                    <Button variant="primary" onClick={() => setShowModal(true)}>
                        Adaugă eveniment
                    </Button>
                </Col>
            </Row>
            <Row>
                {events.map((event) => (
                    <Col md="4" key={event._id}>
                        <Card className="mb-4">
                            <Card.Body>
                                <Card.Title>{event.name}</Card.Title>
                                <Card.Text>
                                    {event.description}
                                    <br />
                                    <strong>Locație:</strong> {event.location}
                                    <br />
                                    <strong>Data:</strong> {new Date(event.date).toLocaleDateString()}
                                    <br />
                                    <strong>Persoană de contact:</strong> {event.contactPerson}
                                    <br />
                                    <img src={event.image} width="100%" alt="Invitația" />
                                </Card.Text>
                                <Button variant="danger" onClick={() => { setSelectedEvent(event); setShowDeleteModal(true); }}>
                                    🗑️
                                </Button>
                                <Button variant="warning" onClick={() => { setSelectedEvent(event); setShowEditModal(true); }}>
                                    ✏️
                                </Button>
                                <Button variant="primary" onClick={() => navigate("/invitation/" + event._id)}>
                                    Invitații
                                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                ))}
            </Row>

            <Modal show={showModal} onHide={() => setShowModal(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>Adaugă eveniment</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Formik
                        initialValues={{
                            name: '',
                            description: '',
                            location: '',
                            date: '',
                            contactPerson: '',
                            image: ''
                        }}
                        validationSchema={Yup.object({
                            name: Yup.string().required('Numele este obligatoriu'),
                            description: Yup.string(),
                            location: Yup.string(),
                            date: Yup.date()
                                .required('Data este obligatorie')
                                .min(new Date(), 'Data trebuie să fie din viitor'),
                            contactPerson: Yup.string().required('Persoana de contact este obligatorie'),
                        })}
                        onSubmit={handleAddEvent}
                    >
                        {({ isSubmitting, handleSubmit, setFieldValue }) => (
                            <Form onSubmit={handleSubmit}>
                                <div className="form-group">
                                    <label htmlFor="name">Nume</label>
                                    <Field name="name" type="text" className="form-control" />
                                    <ErrorMessage name="name" component="div" className="text-danger" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="description">Descriere</label>
                                    <Field name="description" as="textarea" className="form-control" />
                                    <ErrorMessage name="description" component="div" className="text-danger" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="location">Locație</label>
                                    <Field name="location" type="text" className="form-control" />
                                    <ErrorMessage name="location" component="div" className="text-danger" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="date">Data</label>
                                    <Field name="date" type="date" className="form-control" />
                                    <ErrorMessage name="date" component="div" className="text-danger" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="contactPerson">Persoană de contact (nume și telefon)</label>
                                    <Field name="contactPerson" type="text" className="form-control" />
                                    <ErrorMessage name="contactPerson" component="div" className="text-danger" />
                                </div>
                                <div className="form-group col-md-3">
                                    <label htmlFor="img">Image Upload</label>
                                    <Field type="file" name="img" className="form-control" value={undefined} onChange={(event) => {
                                        const type = event.currentTarget.files[0].type.toString()
                                        console.log(type)
                                        if (!type.includes("image")) {
                                            alert("Add a image!");
                                        } else {
                                            setFieldValue("img", event.currentTarget.files[0]); handleImageChange(event)
                                        }
                                    }} />
                                    <ErrorMessage name="img" component="div" className="text-danger" />
                                </div>
                                <div className="form-group col-md-3">
                                    {imageBase64 && <img src={imageBase64} width="100%" alt="Image" />}
                                </div>
                                <Button type="submit" disabled={isSubmitting} className="btn btn-primary">
                                    Adaugă eveniment
                                </Button>
                            </Form>
                        )}
                    </Formik>
                </Modal.Body>
            </Modal>

            <Modal show={showDeleteModal} onHide={() => setShowDeleteModal(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>Confirmare ștergere</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <p>Sunteți sigur că doriți să ștergeți acest eveniment?</p>
                    <Button variant="danger" onClick={handleDeleteEvent}>Șterge</Button>
                    <Button variant="secondary" onClick={() => setShowDeleteModal(false)}>Anulează</Button>
                </Modal.Body>
            </Modal>

            <Modal show={showEditModal} onHide={() => setShowEditModal(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>Editează eveniment</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Formik
                        initialValues={{
                            name: selectedEvent?.name || '',
                            description: selectedEvent?.description || '',
                            location: selectedEvent?.location || '',
                            date: selectedEvent ? new Date(selectedEvent.date).toISOString().split('T')[0] : '',
                            contactPerson: selectedEvent?.contactPerson || '',
                            image: selectedEvent?.image || ''
                        }}
                        enableReinitialize={true}
                        validationSchema={Yup.object({
                            name: Yup.string().required('Numele este obligatoriu'),
                            description: Yup.string(),
                            location: Yup.string(),
                            date: Yup.date()
                                .required('Data este obligatorie')
                                .min(new Date(), 'Data trebuie să fie din viitor'),
                            contactPerson: Yup.string().required('Persoana de contact este obligatorie'),
                            image: Yup.string()
                        })}
                        onSubmit={handleEditEvent}
                    >
                        {({ isSubmitting, handleSubmit, setFieldValue }) => (
                            <Form onSubmit={handleSubmit}>
                                <div className="form-group">
                                    <label htmlFor="name">Nume</label>
                                    <Field name="name" type="text" className="form-control" />
                                    <ErrorMessage name="name" component="div" className="text-danger" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="description">Descriere</label>
                                    <Field name="description" as="textarea" className="form-control" />
                                    <ErrorMessage name="description" component="div" className="text-danger" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="location">Locație</label>
                                    <Field name="location" type="text" className="form-control" />
                                    <ErrorMessage name="location" component="div" className="text-danger" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="date">Data</label>
                                    <Field name="date" type="date" className="form-control" />
                                    <ErrorMessage name="date" component="div" className="text-danger" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="contactPerson">Persoană de contact (nume și telefon)</label>
                                    <Field name="contactPerson" type="text" className="form-control" />
                                    <ErrorMessage name="contactPerson" component="div" className="text-danger" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="image">Image Upload</label>
                                    <Field type="file" name="image" className="form-control" value={undefined} onChange={(event) => {
                                        const type = event.currentTarget.files[0].type.toString()
                                        console.log(type)
                                        if (!type.includes("image")) {
                                            alert("Adaugă o imagine!");
                                        } else {
                                            setFieldValue("image", event.currentTarget.files[0]); handleImageChange(event)
                                        }
                                    }} />
                                    <ErrorMessage name="image" component="div" className="text-danger" />
                                </div>
                                <div className="form-group">
                                    {imageBase64 && <img src={imageBase64} width="100%" alt="Image" />}
                                </div>
                                <Button type="submit" disabled={isSubmitting} className="btn btn-primary">
                                    Salvează modificările
                                </Button>
                            </Form>
                        )}
                    </Formik>
                </Modal.Body>
            </Modal>
        </Container>
    );
};

export default EventsPage;
