import axios from 'axios';

const api = axios.create({ 
  baseURL: (process.env.REACT_APP_SERVER || 'http://localhost:8080') + '/api',  // URL-ul backend-ului
  headers: {
    'Content-Type': 'application/json',
  },
});

// Adaugă un interceptor pentru a insera token-ul de autentificare în fiecare cerere
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers.Authorization = token;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

export default api;