import React, { useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Navbar, Nav, Button } from 'react-bootstrap';
import { useAuth } from '../context/AuthContext';
import logo from './logo.png'

const NavigationBar = () => {
  const { state, dispatch } = useAuth();

  useEffect(() => {
  }, [state.isAuthenticated]);


  const onLogOut = () => {
    localStorage.removeItem('token');
    dispatch({ type: 'SET_AUTHENTICATED', payload: false });
    dispatch({ type: 'SET_ADMIN', payload: false });
    window.location.href = "/login";
  };

  return (
    <Navbar bg="dark" expand="lg" variant="dark">
      <Navbar.Brand href="/" className='mx-3'>
        <img
          alt=""
          src={logo}
          width="30"
          height="30"
          className="d-inline-block align-top"
        />{' '}
        Invitations
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="navbarNav" />
      <Navbar.Collapse id="navbarNav" className='mx-3'>
        <Nav className="me-auto">
          {state.isAuthenticated && (
            <Nav.Link href='/events' className='my-3'>Evenimente</Nav.Link>
          )}
        </Nav>
        <Nav>
          {state.isAuthenticated && (
            <Nav.Link onClick={onLogOut} className="my-2">
              <Button variant="outline-danger">Ieși din cont</Button>
            </Nav.Link>
          )}
          {!state.isAuthenticated && (
            <Nav.Link href="/login" className="my-2">
              <Button variant="outline-primary">Autentificare</Button>
            </Nav.Link>
          )}
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
};

export default NavigationBar;