import 'bootstrap/dist/css/bootstrap.min.css';
import 'react-toastify/dist/ReactToastify.css';
import { Provider } from 'react-redux';
import store from './store';
import Register from './pages/Register';
import Login from './pages/Login';
import { ToastContainer } from 'react-toastify';
import { AuthProvider, useAuth } from './context/AuthContext';
import { Route, Routes, Navigate, Outlet, useLocation } from 'react-router-dom';
import { useEffect } from 'react';
import Home from './pages/Home';
import EventsPage from './pages/EventsPage';
import InvitationsPage from './pages/InvitationsPage';
import ConfirmInvitationPage from './pages/ConfirmInvitationPage';
import EditorPage from './pages/editor/EditorPage';
import { jwtDecode } from 'jwt-decode';
import NavigationBar from './pages/NavigationBar';

function App() {
  useEffect(() => {
    const token = localStorage.getItem("token");

    if (token) {
      try {
        const decodedToken = jwtDecode(token);
        const currentTime = Date.now() / 1000;
        
        if (decodedToken.exp < currentTime) {
          localStorage.removeItem("token");
        }
      } catch (error) {
        localStorage.removeItem("token");
      }
    } 
  }, []);

  return (
    <Provider store={store}>
      <AuthProvider>
        <ToastContainer />
        <NavigationBar />
        <Routes>

          {/* AUTH */}
          <Route exact path="/login" element={<Login />} />
          <Route exact path="/register" element={<Register />} />

          <Route exact path="/" element={<PrivateRoute />}>
            <Route exact path="/" element={<EventsPage />} />
          </Route>

          <Route exact path="/events" element={<PrivateRoute />}>
            <Route exact path="/events" element={<EventsPage />} />
          </Route>

          <Route exact path="/invitation/:eventId" element={<PrivateRoute />}>
            <Route exact path="/invitation/:eventId" element={<InvitationsPage />} />
          </Route>

          <Route exact path="/confirm/:eventId" element={<ConfirmInvitationPage />} />

          <Route exact path="/editor/:eventId" element={<PrivateRoute />}>
            <Route exact path="/editor/:eventId" element={<EditorPage />} />
          </Route>
        </Routes>
      </AuthProvider>
    </Provider>
  );
}

const PrivateRoute = () => {
  const { state } = useAuth();
  const location = useLocation();
  return state.isAuthenticated ? <Outlet /> :
    <Navigate state={{ from: location }} to="/login" />
};

export default App;
