import React, { useEffect, useState } from 'react';
import Select from 'react-select';

const FontPicker = ({ font, setFont }) => {
    const [fonts, setFonts] = useState([]);
    const key = process.env.REACT_APP_GOOGLE_API_KEY;

    useEffect(() => {
        fetch('https://www.googleapis.com/webfonts/v1/webfonts?key=' + key)
            .then(response => response.json())
            .then(data => {
                console.log(data.items)
                const fontOptions = data.items.map(font => ({
                    value: font.family,
                    label: font.family,
                    fontFamily: font.family
                }));
                setFonts(fontOptions);
            });
    }, [key]);

    const customStyles = {
        option: (provided, state) => ({
            ...provided,
            fontFamily: state.data.fontFamily,
        }),
        singleValue: (provided, state) => ({
            ...provided,
            fontFamily: state.data.fontFamily,
        }),
    };

    return (
        <Select
            value={fonts.find(f => f.value === font)}
            onChange={(selectedOption) => setFont(selectedOption.value)}
            options={fonts}
            styles={customStyles}
        />
    );
};

export default FontPicker;