import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { Container, Row, Col, Card, Button, Form } from 'react-bootstrap';
import { toast } from 'react-toastify';
import api from '../api';

const ConfirmInvitationPage = () => {
    const { eventId } = useParams();
    const [event, setEvent] = useState({});
    const [name, setName] = useState('');
    const [status, setStatus] = useState(null);

    useEffect(() => {
        fetchEvent();
    }, []);

    const fetchEvent = async () => {
        try {
            const response = await api.get(`/events/confirm/${eventId}`);
            setEvent(response.data);
        } catch (error) {
            toast.error('Eroare la încărcarea evenimentului: ' + error.message);
        }
    };

    const handleConfirm = async (confirmed) => {
        if(!name){
            toast.warning("Dați un nume!")
            return
        }
        try {
            const response = await api.put(`/invitations/token/${eventId}`, { name, status: confirmed ? 1 : 0 });
            toast.success('Răspunsul a fost salvat cu succes!');
        } catch (error) {
            if(error.code != 500){
                toast.error(error.response.data.message)
            } else toast.error('Eroare la salvarea răspunsului: ' + error.message);
        }
    };

    return (
        <Container>
            <Row className="justify-content-md-center my-3">
                <Col md="8">
                    <Card>
                        <Card.Img variant="top" src={event.image} />
                        <Card.Body>
                            <Card.Title>Trimite răspunsul pentru invitația la: {event.name}</Card.Title>
                            <Card.Text>{event.description}</Card.Text>
                            <Form>
                                <Form.Group controlId="name">
                                    <Form.Label>Numele tău</Form.Label>
                                    <Form.Control
                                        type="text"
                                        value={name} required
                                        onChange={(e) => setName(e.target.value)}
                                    />
                                </Form.Group>
                                <Button variant="success" onClick={() => handleConfirm(true)}>Confirmă</Button>
                                <Button variant="danger" onClick={() => handleConfirm(false)}>Respinge</Button>
                            </Form>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Container>
    );
};

export default ConfirmInvitationPage;
