import React, { useState, useRef } from 'react';
import UploadBackground from './UploadBackground';
import EditableText from './EditableText';
import ExportInvitation from './ExportInvitation';

const InvitationEditor = () => {
    const [background, setBackground] = useState(null);
    const [text, setText] = useState('Damu is turning eighteen soon');
    const [style, setStyle] = useState({
        position: 'absolute',
        top: '50px',
        left: '50px',
        color: 'black',
        fontSize: '24px',
        fontFamily: 'Arial'
    });
    const editorRef = useRef(null);

    return (
        <div>
            <UploadBackground setBackground={setBackground} />
            <div ref={editorRef} style={{ position: 'relative', width: '100%', height: '500px', backgroundImage: `url(${background})`, backgroundSize: 'cover' }}>
                <EditableText text={text} setText={setText} style={style} setStyle={setStyle} />
            </div>
            <ExportInvitation editorRef={editorRef} />
        </div>
    );
};

export default InvitationEditor;